body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
  background-color: rgb(186, 199, 217);
}

html, #root {
  height: 100%;
}

.Weather {
    color: rgb(253, 255, 254);
    width: 50%;
    margin: 30px;
    padding: 10px;
}

.Weather .Weather__Location .Location_detail {
    font-size: 70px;
}

.Weather .Weather__Location .Location_country {
    font-size: 30px;
    font-weight: 200;
}

.Weather .Weather__Summary {
    margin-top: 70px;
    font-size: 30px;
    font-weight: normal;
    text-shadow: 1px 1px #7e7676;
}

.Weather .Weather__Temperature {
    margin: 10px 10px 10px 0;
    font-size: 100px;
    font-weight: 300;
    text-shadow: 1px 1px #7e7676;
}
.Details {
    background-color: rgba(0, 0, 0, 0.5);
    width: 30%;
    height: 50%;
    font-size: 30px;
    float: right;
    margin-right: 5%;
    margin-bottom: 20px;
}

.Details .Details__Table {
    width: 100%;
    border-collapse: collapse;
}

.Details .Details__Table tr {
    border-bottom: 1px solid rgba(0, 0, 0, 0.5);
}

.Details .Details__Table .leftside {
    float: left;
}

.Details .Details__Table .rightside {
    float: right;
}

@media screen and (min-width: 320px) and (max-width: 667px) {
    .Details {
        width: 90%;
    }
}

@media screen and (min-width: 320px) and (max-width: 667px) and (orientation: portrait) {
    .Details {
        width: 90%;
    }    
}

@media screen and (min-width: 768px) and (max-width: 800px) {
    .Details {
        width: 50%;
    }
}



.App--loading {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
}
